import Axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { gagal } from "../../Alert";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;
class FormSaldoAwalHutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch(() =>
        gagal("Sepertinya koneksi bermasalah, mohon periksa koneksi anda")
      );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <Field
              label="Tanggal"
              name="tanggal"
              component={renderField}
              type="date"
            />
          </div>
          <div className="col-lg-3">
            <label htmlFor="">Supplier</label>
            <Field
              label="Supplier"
              name="kodeSupplier"
              component="select"
              className="form-control"
            >
              <option value="DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.state.listSupplier.map((list) => (
                <option
                  value={list.kodeSupplier + "|" + list.namaSupplier}
                  key={list.kodeSupplier}
                >
                  {list.namaSupplier}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-lg-3">
            <Field
              label="Total Bruto"
              name="totalBruto"
              component={renderField}
              type="text"
            />
          </div>
          <div
            className={
              this.props.tipe === "TITIP" ? "col-lg-3 d-none" : "col-lg-3"
            }
          >
            <Field
              label="Total Hutang (Gr)"
              name="totalHutangGr"
              component={renderField}
              type="text"
            />
          </div>
          <div
            className={
              this.props.tipe === "TITIP" ? "col-lg-3 d-none" : "col-lg-3"
            }
          >
            <Field
              label="Total Hutang (Rp)"
              name="totalHutangRp"
              component={renderField}
              type="text"
            />
          </div>
          <div
            className={
              this.props.tipe === "HUTANG" ? "col-lg-3 d-none" : "col-lg-3"
            }
          >
            <Field
              label="Total Titip (Gr)"
              name="totalTitipGr"
              component={renderField}
              type="text"
            />
          </div>
          <div
            className={
              this.props.tipe === "HUTANG" ? "col-lg-3 d-none" : "col-lg-3"
            }
          >
            <Field
              label="Total Titip (Rp)"
              name="totalTitipRp"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormSaldoAwalHutang = reduxForm({
  form: "FormSaldoAwalHutang",
  enableReinitialize: true,
})(FormSaldoAwalHutang);
export default connect()(FormSaldoAwalHutang);
